body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Roboto", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.gitImage {
  width: 25px;
  height: 25px;
}
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px;
  background-color: white;
  display: block;
  max-width: 350px;
  text-align: center;
  height: auto;
  margin: 2%;
  padding: 2%;
}
.card:hover {
  box-shadow: 1px 1px 20px 0 white;
}

.fetchedImg {
  width: 50%;
  height: 50%;
}

.btnFetch {
  background: #ffffff;
  padding: 18px;
  color: #726c6c;
  border: none;
  transition: all 0.5s ease 0s;
}
.btnFetch:hover {
  color: #494949;
  border-radius: 50px;
  border-color: #494949;
  transition: all 0.5s ease 0s;
}
